import React, { useState } from 'react';
import { Pie, PieChart, ResponsiveContainer, Sector } from 'recharts';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import PageContainer from '../../../@jumbo/components/PageComponents/layouts/PageContainer';
import IntlMessages from '../../../@jumbo/utils/IntlMessages';
import {Contracts, Formatter} from '../../../utils/index';
import { useEffect } from 'reactn';

const breadcrumbs = [
    { label: 'Home', link: '/' },
    { label: 'Dashboard', link: '/dashboard' },
    { label: 'Nyan Rewards', isActive: true },
  ];

const useStyles = makeStyles(theme => ({
  root: {
    minWidth: 275,
    width: '100%',
    margin: '0 auto',
    backgroundColor: lighten(theme.palette.background.paper, 0.1),
  },
  bullet: {
    display: 'inline-block',
    margin: '0 2px',
    transform: 'scale(0.8)',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
  text: {
    marginTop: 15
  }
}));

//populate data with info from rewards  contract

function getNativeMsg() {
  if (window.localStorage.getItem('chain') == 'ETH') {
    return <IntlMessages id={'rewards.totaleth'} />
  } else {
    return <IntlMessages id={'rewards.totalBEP'} />
  }
}

function getNativeCoinAvailability() {
  if (window.localStorage.getItem('chain') == 'ETH') {
    return <IntlMessages id={'rewards.ethavailable'} />
  } else {
    return <IntlMessages id={'rewards.bnbavailable'} />
  }
}

function getEstimatedRewardsText() {
  if (window.localStorage.getItem('chain') == 'ETH') {
    return <IntlMessages id={'rewards.estimatedethrewards'} />
  } else {
    return <IntlMessages id={'rewards.estimatedbnbrewards'} />
  }
}

const data = [
  { name: getNativeMsg(), value: 0 },
  { name: <IntlMessages id={'rewards.myshare'} />, value: 0 },
];

const renderActiveShape = props => {
  const RADIAN = Math.PI / 180;
  const { cx, cy, midAngle, innerRadius, outerRadius, startAngle, endAngle, fill, payload, percent, value } = props;
  const sin = Math.sin(-RADIAN * midAngle);
  const cos = Math.cos(-RADIAN * midAngle);
  const sx = cx + (outerRadius + 10) * cos;
  const sy = cy + (outerRadius + 10) * sin;
  const mx = cx + (outerRadius + 30) * cos;
  const my = cy + (outerRadius + 30) * sin;
  const ex = mx + (cos >= 0 ? 1 : -1) * 22;
  const ey = my;
  const textAnchor = cos >= 0 ? 'start' : 'end';

  return (
    <g>
      <text x={cx} y={cy} dy={8} textAnchor="middle" fill={fill}>
        {payload.name}
      </text>
      <Sector
        cx={cx}
        cy={cy}
        innerRadius={innerRadius}
        outerRadius={outerRadius}
        startAngle={startAngle}
        endAngle={endAngle}
        fill={fill}
      />
      <Sector
        cx={cx}
        cy={cy}
        startAngle={startAngle}
        endAngle={endAngle}
        innerRadius={outerRadius + 6}
        outerRadius={outerRadius + 10}
        fill={fill}
      />
      <path d={`M${sx},${sy}L${mx},${my}L${ex},${ey}`} stroke={fill} fill="none" />
      <circle cx={ex} cy={ey} r={2} fill={fill} stroke="none" />
      <text x={ex + (cos >= 0 ? 1 : -1) * 12} y={ey} textAnchor={textAnchor} fill="#333">{`ETH: ${value}`}</text>
      <text x={ex + (cos >= 0 ? 1 : -1) * 12} y={ey} dy={18} textAnchor={textAnchor} fill="#999">
        (<IntlMessages id={'rewards.percent'} />: {`${(percent * 100).toFixed(2)}%)`}
      </text>
    </g>
  );
};

const NyanReward = () => {
  const classes = useStyles();
  const [activeIndex, setActiveIndex] = useState(0);
  const [rewardBalance, setBalance] = useState(0);

  const onPieEnter = (data, index) => {
    setActiveIndex(index);
  };
  useEffect(() => {
    async function loadData() {
      let temp = await Contracts.getNyanRewards();
      setBalance(temp);
      data[0].value = rewardBalance;
    }

    loadData();
  }, []);
  return (
    <PageContainer heading={<IntlMessages id={'rewards.nyanrewards'} />} breadcrumbs={breadcrumbs}>
        <Card className={classes.root}>
            <CardContent>
                <ResponsiveContainer width="100%" height={300}>
                    <PieChart>
                        <Pie
                        dataKey="value"
                        activeIndex={activeIndex}
                        activeShape={renderActiveShape}
                        onMouseEnter={onPieEnter}
                        data={data}
                        innerRadius={60}
                        outerRadius={80}
                        fill="#6200EE"
                        />
                    </PieChart>
                </ResponsiveContainer>
            </CardContent>
            <CardActions>
                <Button size="small"><IntlMessages id={'crypto.viewcontract'} /></Button>
            </CardActions>
        </Card>
        <Card className={classes.root}>
            <CardContent>
                <Typography className={classes.text} variant="h2" gutterBottom component="div">
                  {getNativeCoinAvailability()}: {rewardBalance}
                </Typography>
                <Typography className={classes.text} variant="h2" gutterBottom component="div">
                  {getEstimatedRewardsText()}: 0
                </Typography>
                <Button disabled variant="outlined" size="large" color="primary" className={classes.margin}>
                  <IntlMessages id={'rewards.claim'} />
                </Button>
            </CardContent>
        </Card>
    </PageContainer>
  );
};

export default NyanReward;
